.accordion-collapse{
  background-color: wheat;
}
button.accordion-button {
  background-color: tan !important;
  color: black !important ;
}
.textview {
  font-size: 16px;
  line-height: 24px;
}
.dlw{
  color: rgb(54, 54, 219);
  cursor: pointer;
  font-weight:600;
}
.dlw:hover{
  color: blue;
}
.prettyprint {
  border-radius: 5px;
  border: 1px solid #ddd !important;
  background: #f4fbf4;
  font-family: Menlo,Bitstream Vera Sans Mono,DejaVu Sans Mono,Monaco,Consolas,monospace;
  border: 0!important;
  max-width: 100%;
    max-height: 600px;
    display: block;
    overflow: auto;
    padding: 8px;
    font-size: 14px;
    margin-bottom: 16px;
    border: 1px solid #eee;
    -webkit-overflow-scrolling: touch;
    margin-bottom: 10px;
}
.tlv{
  position: absolute;
  top: 0px;
  right: 0px;
  background-color: #d9d6d3;
  padding: 1px;
  border-radius: 5px;
  border: 0.5px solid #8b8686;
  cursor: pointer;
}
.tlv:hover{
  background-color: #ffde7c;
}